/* ------------------------------------------------------------ *\
	Footer-payments
\* ------------------------------------------------------------ */

.payments {
    width: 251px;
    margin: 20px auto;
    & ul li {
        display: inline-block;
        & img {
            width: 93px;
            height: 36px;
            &.round-border {
                border-radius: 6px;
                width: 58px;
            }
            &.image-center {
                width: 58px;
                height: 36px;
                margin-bottom: 22px;
                margin-left: 2px;
            }
        }
    }
}

@media #{$desktop} {
    .payments {
        display: inline-block;
        vertical-align: middle;
    }
}